import React from "react";
import { useMoralis, useNFTBalances } from "react-moralis";
import { Button, Card, Image, Tooltip, Skeleton } from "antd";
import { FileSearchOutlined, ShoppingCartOutlined } from "@ant-design/icons";
import { getExplorer } from "helpers/networks";
import { useVerifyMetadata } from "hooks/useVerifyMetadata";
import { getEllipsisTxt } from "../helpers/formatters";
import { mainBackgroundCol, brightFontCol } from "GlobalStyles";
import { Divider } from "antd";
import { Link } from "react-router-dom";

const { Meta } = Card;

const styles = {
  NFTs: {
    display: "flex",
    flexWrap: "wrap",
    WebkitBoxPack: "start",
    justifyContent: "flex-start",
    margin: "0 auto",
    maxWidth: "1000px",
    width: "100%",
    gap: "10px",
  },
};

function NFTBalance() {
  const { data: NFTBalances } = useNFTBalances();
  console.log('fetching NFTBalances', NFTBalances)
  const { chainId } = useMoralis();
  const { verifyMetadata } = useVerifyMetadata();

  console.log("NFTBalances", NFTBalances);
  return (
    <div style={{
      padding: "15px",
      maxWidth: "1030px",
      width: "100%",
      background: mainBackgroundCol,
      color: brightFontCol,
    }}>
      <h1>🖼 Your Avatars and Wearables</h1>
      <br />
      <h3>
        If you have your MGL Avatar NFT 🙂
      </h3>
      <h3>
        Then you will be albe to 🎮{" "}{" "}Play in MetaGymLand Metaverse
      </h3>
      <h3>
        by clicking <b style={{ color: "#1990FF" }}>[play with me]</b> button{" "}🔘
      </h3>
      <h3>
        If you don't have your awesome Avatar yet, get one in our
        {" "}<Link to="/marketplace">
          <b><u>Marketplace</u></b>
        </Link>{" "}🚀
      </h3>
      {NFTBalances?.result &&
        <Divider style={{ backgroundColor: brightFontCol }} />}
      <div style={styles.NFTs}>
        <Skeleton loading={!NFTBalances?.result}>
          {NFTBalances?.result &&
            NFTBalances.result
              .filter(nft => nft.image)
              .map((nft, index) => {
                //Verify Metadata
                nft = verifyMetadata(nft);
                return (
                  <Card
                    hoverable
                    actions={[
                      <Tooltip title="View On Blockexplorer">
                        <FileSearchOutlined
                          onClick={() => window.open(`${getExplorer(chainId)}address/${nft.token_address}`, "_blank")}
                        />
                      </Tooltip>,
                      <Tooltip title="List for Sale">
                        <ShoppingCartOutlined onClick={() => alert("Smart contract integration")} />
                      </Tooltip>,
                    ]}
                    style={{ width: 240, border: "2px solid #e7eaf3" }}
                    cover={
                      <Image
                        preview={false}
                        src={nft?.image || "error"}
                        fallback="data:image/png;base64,"
                        alt=""
                        style={{ height: "300px" }}
                      />
                    }
                    key={index}
                  >
                    <Meta title={nft.name} description={
                      <>
                        <span><b>{getEllipsisTxt(nft.token_address, 7)}</b></span>
                        &nbsp;
                        <span>id: <b>{nft.token_id}</b></span>
                      </>
                    } />
                    <br />
                    <Button
                      onClick={() => alert('will play')}
                      type="primary"
                      style={{ marginLeft: "20%" }}
                    >play with me
                    </Button>
                  </Card>
                );
              })}
        </Skeleton>
      </div>
    </div>
  );
}

export default NFTBalance;
